.inputForm
{
    background-color: aquamarine;
    box-shadow:-1px 6px 46px 0px rgba(54,54,54,0.75);
    margin-top:50px;
    padding:20px;
    border-radius: 5px;
}
.inputForm input{
     width:300px;
     border:1px solid white;
     padding: 10px 10px;
     outline:none;
     margin-bottom: 20px;
     font-size:16px;
}
.inputForm textarea{
     width:300px;
     border:1px solid white;
     padding:10px 10px;
     outline:none;
     margin-bottom:20px;
     font-size:16px;
}
.inputForm button{
    background-color: #002b5b;
    color:white;
    border:none;
    padding: 5px 25px;
    font-weight: bold;
}
